            <template slot-scope="scope">
              <el-button type="text" size="small" @click="Delete_Alert">
                <p class="text-red-500">刪除</p>
              </el-button>

              <el-button type="text" size="small" @click="getData">編輯</el-button>
            </template>
          </el-table-column>
