  methods: {
    // 刪除確認
    Delete_Alert() {
      // Use sweetalert2
      this.$swal
        .fire({
          title: "你確定嗎？",
          text: "資料刪除後將無法復原",
          icon: "warning",
          showCancelButton: true,
          cancelButtonColor: "#0084ff",
          confirmButtonColor: "#cccccc",
          confirmButtonText: "確定刪除",
          cancelButtonText: "取消",
          closeOnConfirm: false,
        })
        .then((result) => {
          if (result.isConfirmed) {
            this.$swal.fire("刪除成功!", "您的檔案已成功刪除", "離開");
          }
        });
    },

    // 讀取資料
    getData() {
      this.$axios
        .get("@/static/data.json")
        .then((res) => {
          console.log(res);
        })
        .catch((error) => {
          console.log(error);
        });

      this.$router.push("/seat_edit");
    },
  },
};
</script>

